#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-family: "Comfortaa", cursive;
}

.players > div {
  display: inline-block;
  border: 3px solid wheat;
  font-size: 30px;
  padding: 5px 10px;
  min-width: 250px;
  margin: 0 0 10px 0;
  cursor: pointer;
}

.game-board,
.game-board-wooden,
.game-board-cartoon {
  display: flex;
  width: 640px;
  height: 663px;
  border: 16px solid #654321;
  border-width: 16px 16px 0 16px;
  position: relative;
  margin: auto;
}

.game-board-letter {
  position: absolute;
  left: -51%;
  top: 311px;
  transform: rotate(90deg);
  font-size: 17px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.game-board-number {
  position: absolute;
  top: 640px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #654321;
}

.game-board-number span,
.game-board-letter span {
  display: block;
  width: 80px;
  text-align: center;
}

.game-status {
  display: block;
  padding: 5px;
  background-color: #fff;
  border: 5px solid #deb887;
  width: 55%;
  max-width: 500px;
  font-size: 30px;
  position: relative;
  top: 15px;
  z-index: 3;
  margin: 0 auto;
}

.time-travel {
  max-width: 660px;
  display: block;
  margin: 0 auto;
  text-align: right;
}

.undo {
  font-size: 15px;
  padding: 5px;
  border-radius: 0;
  background-color: #fff;
  box-shadow: none;
  border: 3px solid #deb887;
  margin: 5px;
  cursor: pointer;
}

.undo.disabled {
  cursor: not-allowed;
  opacity: 0.4;
}

.board-col {
  max-width: 80px;
}

.square {
  background: no-repeat #fff;
  border: none;
  height: 80px;
  width: 80px;
  padding: 5px;
  background-origin: content-box;
  background-size: contain;
}

.square div {
  width: 54px;
  height: 54px;
}
.isActive {
  background-color: wheat;
}

.square.movable.player1-move,
.square.movable.player2-move {
  background-color: #ff0000;
}

.square.player1,
.square.player2,
.square.movable {
  cursor: pointer;
}
.game-board-wooden .square.black {
  padding: 0;
  background-image: url("./img/white_square.jpg");
  background-size: 100% 100%;
}
.game-board .square.black {
  padding: 0;
  background-color: #ffffff;
}
.game-board-wooden .square.white {
  padding: 0;
  background-image: url("./img/black_square.jpg");
  background-size: 100% 100%;
}
.game-board .square.white {
  padding: 0;
  background-color: #262626;
}
.square.player1 div {
  margin: 0 9px;
  background: #000000;
  border-radius: 60%/45%;
  position: relative;
}
.square.player1 div:before {
  content: "";
  position: absolute;
  top: 20%;
  bottom: 20%;  
  background: inherit;
  border-radius: 5%/50%;
}
/* .square.player1 div {
  margin: 0 9px;
  border-radius: 50%;
  border: 4px solid #000000;
  cursor: pointer;
  background-color: #1a1919;
} */
.square.player2 div {
  margin: 0 9px;
  border-radius: 50%;
  border: 4px solid white;
  cursor: pointer;
  background-color: #dbdbdb;
}
.square.player1.king {
  margin: 0 9px;
  background-image: url("./img/red-king.png");
}
.square.player2.king {
  margin: 0 9px;
  background-image: url("./img/blue-king.png");
}

@media only screen and (max-width: 750px) {
  .game-board {
    width: 440px;
    height: 463px;
  }
  .game-board-letter {
    left: -52%;
    top: 207px;
    width: 440px;
  }
  .game-board-number {
    top: 440px;
    width: 440px;
  }
  .board-col {
    max-width: 55px;
  }
  .square {
    height: 55px;
    width: 55px;
  }
  .square div {
    width: 29px;
    height: 29px;
  }
}

.slider-wrapper {
  margin: 10px 0;
}
